@import "../setup/vars";

.icon {
    cursor: pointer;
}

.flex {
    display: flex;
    &.justify-center {
        justify-content: center;
    }
    &.align-center {
        align-items: center;
    }
    &.col {
        flex-direction: column;
    }
    &.space-between {
        justify-content: space-between;
    }
    &.space-around {
        justify-content: space-around;
    }
    &.space-evenly {
        justify-content: space-evenly;
    }
    &.wrap {
        flex-wrap: wrap;
    }
    &.align-baseline {
        align-items: baseline;
    }
    &.justify-baseline {
        justify-content: flex-end;
    }
}
.ml20 {
    margin-left: 20px;
}
.mt20 {
    margin-top: 20px;
}
.mb100 {
    margin-bottom: 100px;
}
.mr {
    margin-right: 20px;
}

.section {
    display: flex;
    padding: 100px 150px;
}
.section-title {
    flex-shrink: 0;
    width: 200px;
    margin-right: 70px;
    color: $logo;
    text-transform: uppercase;
    text-align: right;
    font-weight: 700;
    letter-spacing: 2px;
}
.section-content {
    font-weight: 300;
    font-size: 1rem;
    width: 100%;
    max-width: 650px;
}

